.basket-container {
  position: fixed;
  top: 5.5rem;
  right: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  cursor: pointer;
}
.cart-quantity {
  position: fixed;
  top: 7rem;
  right: 33px;  
  font-size: 1.3rem;
  padding-left: 20%;  
  min-height: 22px;
  font-weight: 700 !important;
  color: white;
}

  /* basketList-container */

.modal-wrapper {
  background-color: #c4d4cce8;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2;
}
.basketList-container {
  z-index: 3;
  background-color: rgb(240 246 245 / 97%);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  max-width: 50rem;
  width: 100%;
  max-height: 50rem;
  display: flex;
  flex-direction: column;   
  animation: showBasket 400ms ease-in-out;
}
.basket-content {
  overflow-y: auto;
}
.close-icon {
  padding: 1rem;
  align-self: end;
  font-size: 3rem;
  cursor: pointer;  
  transition: transform 1000ms ease-in-out; 
}
.modal-wrapper .close-icon:hover {  
  transform: rotate(180deg);
  transition: transform 1000ms ease-in-out;  
}
.emptyButton-remember {
  padding: 0 14px;;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
}

@keyframes showBasket { 
  from {
    top: -100vh;
    opacity: 20%;
  }
  to {
    opacity: 1;
    top: 50%;
  }
}

@media (max-width: 480px) {
  .basketList-container {   
    top: 0;
    left: 0;
    transform: translate(0, 0);    
    animation: showBasket 0ms ease-in-out;
    max-height: 100vh;
  }
  .close-icon {
    padding: 0.3rem;    
  }
  .basket-content {
    margin: 0;
  }
  .basket-totalInfo {
    padding: 1rem !important;
  }
}